.vtimeline {
  list-style: none;
  padding: 0;
  position: relative;
  margin-bottom: 20px;
}
.vtimeline:before {
  top: 15px;
  bottom: 0;
  position: absolute;
  content: " ";
  width: 3px;
  background-color: rgb(var(--light-rgb));
  inset-inline-start: 50%;
  margin-inline-start: -1.5px;
  max-height: calc(100% - 9.375rem);
}
.vtimeline .timeline-wrapper {
  display: block;
  margin-bottom: 20px;
  position: relative;
  width: 100%;
  padding-inline-end: 90px;
}
.vtimeline .timeline-wrapper:before {
  content: " ";
  display: table;
}
.vtimeline .timeline-wrapper:after {
  content: " ";
  display: table;
  clear: both;
}
.vtimeline .timeline-wrapper .timeline-panel {
  border-radius: 2px;
  padding: 20px;
  position: relative;
  background: rgb(var(--light-rgb));
  border-radius: 0px;
  box-shadow: 0 0px 3px rgba(162, 169, 204, 0.24);
  width: 35%;
  margin-inline-start: 15%;
}
