  position: absolute;
  top: 0;
  width: 100%;
  height: 2px;
  content: "";
  inset-inline-start: 0;
  inset-inline-end: 0;
}
.vtimeline .timeline-wrapper .timeline-panel:after {
  position: absolute;
  top: 10px;
  inset-inline-end: -14px;
  display: inline-block;
  border-top: 14px solid transparent;
  border-inline-start: 14px solid rgb(var(--light-rgb));
